body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.homepage {
  background: eggshell;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Trebuchet MS";
  font-size: 80px;
  background: -webkit-radial-gradient(center, ellipse, rgba(30,87,153,0.1) 0%,rgba(125,185,232,0) 100%);
  background: radial-gradient(ellipse at center, rgba(30,87,153,0.1) 0%,rgba(125,185,232,0) 100%); ;
}

.homepage a {
  text-decoration: none;
  color: #626567;
}

.homepage-choice-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 80%;
  max-width: 850px;
  height: 50%;
}

.homepage-big {
  flex-grow: 1;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  -webkit-transition: all linear 50ms;
  transition: all linear 50ms;
  cursor: pointer;
  border-top: solid #B3B6B7 1px;
  border-bottom: solid #B3B6B7 1px;
  color: #7B7D7D;
}

.homepage-big:hover {
  -webkit-transform: scale(1.03);
          transform: scale(1.03);
  border: rgb(255, 235, 119) solid 2px;
  border-radius: 8px;
  box-shadow: rgba(235, 213, 90, 0.3) 0px 0px 90px 12px;
}

.homepage-big-left {
  background: #FDFEFE;
  border-top-left-radius: 8px;
  border-bottom-left-radius: 8px;
  border-left: solid #B3B6B7 1px;
}

.homepage-big-right {
  background: #F7F9F9;
  border-top-right-radius: 8px;
  border-bottom-right-radius: 8px;
  border-right: solid #B3B6B7 1px;
}

@media only screen and (max-width: 600px) {
  .homepage {
    font-size: 40px;
  }

  .homepage-choice-wrapper {
    flex-direction: column;
  }

  .homepage-big {
    width: 80%;
  }

  .homepage-big {
    border-left: solid #B3B6B7 1px;
    border-right: solid #B3B6B7 1px;
  }

  .homepage-big-left {
    border-top-left-radius: 8px;
    border-top-right-radius: 8px;
    border-bottom-left-radius: 0px;
    border-bottom: none;
    border-top: solid #B3B6B7 1px;
  }
  
  .homepage-big-right {
    border-bottom-right-radius: 8px;
    border-bottom-left-radius: 8px;
    border-top-right-radius: 0px;
    border-top: none;
    border-bottom: solid #B3B6B7 1px;
  }
}
.sky-sphere-center {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  -webkit-transform: translateY(8%);
          transform: translateY(8%);
}

.sky-sphere {
  border-radius: 100%;
  background-color: white;
  position: absolute;
  bottom: 0;
  left: 0;
  box-shadow: 0 0 30px #9ecaed;
  -webkit-transition: -webkit-transform 300ms linear;
  transition: -webkit-transform 300ms linear;
  transition: transform 300ms linear;
  transition: transform 300ms linear, -webkit-transform 300ms linear;
}

.sky-sphere-hue {
  box-shadow: 0 0 80px #e5ff6f;
}
.stars {
  -webkit-animation: moveUpDown 100s linear infinite;
          animation: moveUpDown 100s linear infinite;
  width: 1px;
  height: 1px;
  background: transparent;
  position: absolute;
  top: 0;
  opacity: 0.7;
  -webkit-transition: opacity 2s linear;
  transition: opacity 2s linear;
}
.stars::after {
  content: " ";
  position: absolute;
  top: 0;
  width: 1px;
  height: 1px;
  background: transparent;
}

@-webkit-keyframes spin {
  100% {
    -webkit-transform:rotate(360deg);
            transform:rotate(360deg);
  }
}

@keyframes spin {
  100% {
    -webkit-transform:rotate(360deg);
            transform:rotate(360deg);
  }
}

@-webkit-keyframes moveUpDown {
  0%, 100% {
    bottom: 0;
  }
  50% {
    bottom: 100px;
  }
}

@keyframes moveUpDown {
  0%, 100% {
    bottom: 0;
  }
  50% {
    bottom: 100px;
  }
}
.time-widget-wrapper {
  background-color: black;
  border-radius: 100%;
  position: relative;
  overflow: hidden;
}

.sky {
  background-color: lightblue;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.time-land {
  width: 100%;
  height: 100%;
  background-color: green;
  border-radius: 100%;
}

.time-land-wrapper {
  width: 100%;
  height: 50%;
  bottom: 0;
  position: absolute;
  background: darkgreen;
  border-radius: 100%;
  -webkit-transform: translateY(100%) scale(2);
          transform: translateY(100%) scale(2);
  z-index: 10;
}

.light-filter {
  width: 100%;
  height: 100%;
  -webkit-transition: opacity 2s linear;
  transition: opacity 2s linear;
  position: absolute;
  top: 0;
  left: 0;
}

.land {
  border-radius: 100%;
}

.time-land-night {
  background: rgb(3, 22, 3);
}
.time-land-night-peak {
  background: rgb(0, 48, 0);
}
.time-land-morning {
  background: -webkit-linear-gradient(top, #83c783 6%,#52b152 16%,#008a00 23%,#005700 100%);
  background: linear-gradient(to bottom, #83c783 6%,#52b152 16%,#008a00 23%,#005700 100%);;
}
.time-land-morning-peak {
  background: -webkit-linear-gradient(top, #83c783 6%,#52b152 16%,#008a00 23%,#005700 100%);
  background: linear-gradient(to bottom, #83c783 6%,#52b152 16%,#008a00 23%,#005700 100%);;
}
.time-land-noon {
  background: -webkit-linear-gradient(top, #83c783 6%,#52b152 16%,#008a00 23%,#005700 100%);
  background: linear-gradient(to bottom, #83c783 6%,#52b152 16%,#008a00 23%,#005700 100%);;
}
.time-land-noon-peak {
  background: -webkit-linear-gradient(top, #83c783 6%,#52b152 16%,#008a00 23%,#005700 100%);
  background: linear-gradient(to bottom, #83c783 6%,#52b152 16%,#008a00 23%,#005700 100%);;
}
.time-land-evening {
  background: -webkit-linear-gradient(top, #83c783 6%,#52b152 16%,#008a00 23%,#005700 100%);
  background: linear-gradient(to bottom, #83c783 6%,#52b152 16%,#008a00 23%,#005700 100%);;
}
.time-land-evening-peak {
  background: rgb(22, 56, 22);
}

.time-sky-night {
  background: rgb(0, 0, 66);
}
.time-sky-night-peak {
  background: black;
}
.time-sky-morning {
  background: rgb(0, 0, 133);;
}
.time-sky-morning-peak {
  background: -webkit-linear-gradient(#C6E4EE 0%, #C6E4EE 40%, #FED1AE 60%, #FAA0B9 70%, #CB7DCB 80%, #757ECB 100%);
  background: linear-gradient(#C6E4EE 0%, #C6E4EE 40%, #FED1AE 60%, #FAA0B9 70%, #CB7DCB 80%, #757ECB 100%);
}
.time-sky-noon {
  background: lightblue;
}
.time-sky-noon-peak {
  background: lightblue;
}
.time-sky-evening {
  background: -webkit-linear-gradient(#C6E4EE 0%, #C6E4EE 40%, #FED1AE 60%, #FAA0B9 70%, #CB7DCB 80%, #757ECB 100%);
  background: linear-gradient(#C6E4EE 0%, #C6E4EE 40%, #FED1AE 60%, #FAA0B9 70%, #CB7DCB 80%, #757ECB 100%);
}
.time-sky-evening-peak {
  background: darkblue;
}
.the-button {
  padding: 10px 35px;
  background: cornflowerblue;
  border: none;
  color: ghostwhite;
  font-family: monospace;
  color: #fff;
  background-color: #6496c8;
  border-radius: 15px;
  box-shadow: 0 5px #27496d;
  font-size: 1.5em;
  cursor: pointer;
  position: fixed;
  -webkit-transform: translateX(-50%) translateY(-50%);
          transform: translateX(-50%) translateY(-50%);
  -webkit-transition: top linear 0.5s, left linear 0.5s;
  transition: top linear 0.5s, left linear 0.5s;
}

.the-button:disabled {
  cursor: not-allowed;
  background: red;
  box-shadow: 0 5px darkred;
}

.the-rest {
  width: 100%;
  height: 100vh;
  background-color: LightYellow;
  overflow: hidden;
}

.rhyme-word-wrapper {
  font-size: 25px;
  overflow-x: hidden;
  padding: 2%;
  padding-bottom: 0;
  white-space: nowrap;
  border-bottom: 5px solid #B3B6B7;
}

.rhyme-word {
  font-size: 40px;
  display: inline-block;
  margin-right: 2%;
  color: #B3B6B7;
}

.rhyming-button {
  position: fixed;
  right: 10px;
  width: 100px;
  height: 50px;
  cursor: pointer;
  color: white;
  font-size: 15px;
  border: none;
}

.rhyming-button:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
  z-index: 100;
}

.rhyming-trigger {
  top: 10px;
  background-color: green;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
}

.rhyming-reset {
  top: 60px;
  background-color: red;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
}

.one-of-many-rhymes {
  color: #626567;
  cursor: pointer;
}

.one-of-many-rhymes:hover {
  -webkit-transform: scale(2);
          transform: scale(2);
}
.container {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: lightgoldenrodyellow;
  border-radius: 100%;
  height: 150px;
  width: 150px;
}

.little-button {
  width: 30px;
  height: 15px;
  background-color: cornflowerblue;
  border-radius: 4px;
}

.little-button:hover {
  background-color: red;
}
.rhymes-preview {
  background: ghostwhite;
  width: 150px;
  height: 150px;
  border-radius: 100%;
  overflow: hidden;
}

.rhyme-line {
  width: 100%;
  height: 5px;
  background-color: #B3B6B7;
  margin-bottom: 8px;
}
.showcase {
  padding: 5px;
  border-radius: 100%;
  box-shadow: 5px 0px 20px #888888;
  opacity: 0.8;
  -webkit-transition: opacity 300ms ease-out;
  transition: opacity 300ms ease-out;
}

.showcase a {
  color: inherit;
}

.showcase:hover {
  opacity: 0.95;
}

.showcase-organizer {
  display: flex;
  align-items: center;
  flex-direction: column;
  margin-bottom: 40px;
}

.showcase-metadata {
  margin-top: 20px;
  text-align: center;
}

.showcase-title {
  font-size: 40px;
}
.play {
  background: eggshell;
  padding: 5%;
  text-align: center;
  color: #626567;
}

.play-projects {
  display: flex;
  justify-content: space-around;
  max-width: 900px;
  margin: 0 auto;
  margin-top: 10%;
}

.showcase-wrapper {
  margin-right: 20px;
}

@media only screen and (max-width: 600px) {
  .play-projects {
    flex-direction: column;
  }

  .showcase-wrapper {
    margin-right: 0;
  }
}
.profile {
  font-family: 'Pacifico', cursive;
  font-size: 30px;
  text-decoration: underline;
  height: 53px;
  padding: 0 20px;
  background-color: black;
  border-radius: 6px;
  -webkit-transition: background-color 2s ease-in;
  transition: background-color 2s ease-in;
  border: solid black 2px;
}

.profile-loaded {
  background-color: white;
}

.profile a {
  color: inherit;
}
.nav-bar {
  width: 100%;
  position: fixed;
  bottom: 0;
  left: 0;
  display: flex;
  justify-content: center;
  z-index: 100;
  opacity: 0.1;
  -webkit-transition: opacity 600ms ease-out;
  transition: opacity 600ms ease-out;
  padding-bottom: 2%;
}

.nav-bar:hover {
  opacity: .65;
}

.nav-bar-placeholder {
  width: 100%;
  visibility: hidden;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

